<template>

  <div class="top-bar">
    <router-link @click.native="toHome()" class="clickIcon" to="/" tag="div"></router-link>
    <nav class="top-nav">
      <router-link class="link" to="/data-analysis">{{$t('lang.dataStore')}}</router-link>
      <router-link class="link" to="/about">{{$t('lang.aboutUs')}}</router-link>
      <router-link class="link" to="/source">{{$t('lang.dataSource')}}</router-link>
      <img v-if="lang==='zh-CN'" @click="event().changeLangEvent()" style="margin-left:41px;cursor: pointer;" width="64px" height="32px" src="../assets/images/chinese.png" alt="">
      <img v-if="lang==='en-US'" @click="event().changeLangEvent()" style="margin-left:41px;cursor: pointer;" width="64px" height="32px" src="../assets/images/english.png" alt="">
    </nav>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';
export default {
  data: () => {
    return {
      activeIndex: -1,
      lang: "zh-CN",
      language: true, //true:中文，false:英文
      navList: [
        { menu: "lang.dataStore", router: "/data-analysis" },
        { menu: "lang.aboutUs", router: "/about" },
        { menu: "lang.dataSource", router: "/source" }
      ]
    };
  },
  // created(){
  //   if (localStorage.getItem('lang')) {
  //     this.lang = localStorage.getItem('lang')
  //     this.$i18n.locale = localStorage.getItem('lang')
  //   }
  // },
  activated() {
    if (localStorage.getItem("lang")) {
      this.lang = localStorage.getItem("lang");
      this.$i18n.locale = localStorage.getItem("lang");
    }
  },
  methods: {
    ...mapMutations(['changeLang']),
    toHome() {
      this.activeIndex = -1;
      console.log(this.activeIndex);
    },
    event() {
      return {
        changeLangEvent: () => {
          if (this.lang === "zh-CN") {
            localStorage.setItem("lang", "en-US");
            this.changeLang('en')
            this.lang = "en-US";
            this.$i18n.locale = this.lang; //关键语句
          } else {
            localStorage.setItem("lang", "zh-CN");
            this.changeLang('cn')
            this.lang = "zh-CN";
            this.$i18n.locale = this.lang; //关键语句
          }
        },
        selectMenu: index => {
          this.activeIndex = index;
        }
        //事件
      };
    }
  }
};
</script>

